.b-why {
  position: relative; }
  .b-why__wrap {
    min-height: 100vh;
    display: flex;
    flex-wrap: wrap; }

.el-why {
  font-family: "Circe";
  min-height: 50vh;
  flex: 1 0 49vw;
  background: center/cover no-repeat;
  font-size: 16px;
  color: white; }
  .el-why:nth-child(2n + 1) .el-why__body {
    margin: 0 0 0 auto; }
  .el-why:nth-child(1), .el-why:nth-child(2) {
    display: flex;
    align-items: flex-end; }
  .el-why__body {
    max-width: 625px;
    padding: 87px 75px 120px;
    width: 100%; }
  .el-why__title {
    font-weight: 700;
    margin-bottom: 28px; }
  .el-why__desc {
    font-weight: 300; }
  .el-why__content {
    max-width: 236px; }
